.section1{
    .wrapper1 {
        background: rgb(0, 0, 0);
        color: white;
        position: relative;
        width: 100%;
        padding: 0;
        @media screen and (max-width: 426px){
            bottom: 40%;
            height: 85vh;
        }

        @media screen and (min-width: 426px) and (max-width: 768px){
            height: 100vh;
            padding: 0;
        }

        @media screen and (min-width: 426px) and (max-width: 1440px){
            height: 120vh;
            padding: 0;
        }

        .section1-background{
            opacity: 0.4;
            width: 100%;
            height: 100%;
        }

        .section1__contents-block{
            position: absolute;
            left: 5%;

            .section1__text {
                line-height: 5.3rem;
                font-family: roboto, sans-serif;
                opacity: 0;
                animation: slideFadeIn1 .6s forwards linear;
            }

            @media screen and (max-width: 426px){
                bottom: 20%;
                .section1__text {
                    font-size: 1.9rem;
                    line-height: 2rem;
                    width: 89%;
                    margin: 0 0 15%;
                }
            }

            @media screen and (min-width: 426px) and 
            (max-width: 768px){
                bottom: 12%;

                .section1__text {
                    font-size: 3rem;
                    line-height: 4rem;
                    width: 89%;
                    margin: 0 0 15%;
                }
            }

            @media screen and (min-width: 769px) and (max-width: 1024px){
                bottom: 20%;
                left: 5%;
                
                .section1__text{
                    font-size: 2.8rem;
                    line-height: 3.5rem;
                    width: 85%;
                    margin: 0 0 5%;
                }
            }

            @media screen and (min-width: 1025px){
                .section1__text{
                    font-size: 4.6rem;
                    line-height: 5rem;
                    width: 70%;
                    margin: 0 0 5%;
                }
            }   


            .section1__btn-block {
                opacity: 0;
                animation: slideFadeIn2 .6s .4s forwards linear;
            }
    
            .section1__btn {
                @include buttonStyle;
            }

        }
    }
}

