.section4 {
    padding: 8rem 6rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
    
    .section4__contents-block{
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        margin-bottom: 12%;

        .section4__contents__title {
            font-size: 2.3rem;
            margin: 2rem auto;
            text-align: center;
        }

        .section4__contents__desc {
            width: 70%;
            text-align: center;
            color: #727272;
        }
    }

    .section4__imgs-block {
        display: grid;
        // grid-template-columns: repeat(3, 1fr);  //창 크기에 따라 width 변화됨

        //1440px / 1224px / 
        grid-template-columns: repeat(3, 20rem);
        grid-gap: 2rem;
        // background: gray;
        grid-auto-rows: 350px;
        justify-items: center;
        
        .img-box {
            border-radius: 5px;
        }

        .img-box img{
            min-width: 100%;
            min-height: 100%;
            border-radius: inherit;
        }

        .img-box:nth-child(1) {
            grid-column: 1/3;
        }

        .img-box:nth-child(odd) {
            // border: 1px solid red;
        }

        .img-box:nth-child(even) {
            // border: 1px solid blue;
        }
    }

    @media screen and (max-width: 576px){
        padding: 8rem 3%;

        .section4__contents-block{
            .section4__contents__desc {
                width: 100%;
            }
        }

        .section4__imgs-block {
            grid-template-columns: repeat(1, 100%);
            grid-gap: 1.8rem;
            // background: gray;
            grid-auto-rows: minmax(200px,auto);
            
            .img-box:nth-child(odd) {
                grid-column: auto;
            }
        }
    
    }

@media screen and (min-width: 576px) and (max-width: 768px){
    padding: 8rem 2%;

    .section4__imgs-block {
        grid-template-columns: repeat(1, 32rem);
        grid-gap: 1.8rem;
        // background: gray;
        grid-auto-rows: minmax(300px,auto);
        
        .img-box:nth-child(odd) {
            grid-column: auto;
        }
    }

}

@media screen and (min-width: 768px) and (max-width: 992px){
    padding: 8rem 1rem;

    .section4__contents-block{
        .section4__contents__desc {
            width: 95%;
        }
    }

    .section4__imgs-block {

        grid-template-columns: repeat(4, 9.5rem);
        grid-gap: 1.8rem;
        // background: gray;
        grid-auto-rows: 330px;
        .img-box:nth-child(odd) {
            grid-column: 3/5;
        }

        .img-box:nth-child(even) {
            grid-column: 1/3;
        }

        .img-box:nth-child(1) {
            grid-column: 1/5;
        }
    }
}

